 * name: HouseRoomInfo
 * description:
 * author: roct
 * date: 18:28 2021/4/17 0017
 */
import { Component, Prop, Vue, Watch } from 'vue-property-decorator'
import { getRoomEditAll, fastAdd } from '@/api/manage/sys/housePageApi'
import HouseRoomItem from './HouseRoomItem.vue'
import {
  getDictSelect,
  getRoomTypeList,
  getBuildingSelect,
  updateRoomInfo
} from '@/api/manage/sys/housePageApi'

export interface ParamsType {
  area: string | number // 平米
  buildingNo: number | string
  floorPrice: number | string
  name: number | string
  paymentMethod: number | string
  price: number | string
  projectId: number | string
  roomNoList: number[] | string[]
  roomTypeId: number | string
  status: number | string
  toward: string
}

@Component({
  components: {
    HouseRoomItem
  }
})
export default class HouseRoomInfo extends Vue {
  // 房型id
  // @Prop({ type: [Number, String], default: '' })
  private roomTypeId: number | string = ''
  // 项目id
  @Prop({ type: [Number, String], default: '' })
  private projectId!: number | string
  // 项目编号
  private projectNo: number | string = -1
  //buildNo
  private buildingNo: number | string = ''
  // 楼栋列表
  private buildNoList: any[] = []
  // 启用还是停用
  private statusList = [
    {
      name: '启用',
      value: '1'
    },
    {
      name: '停用',
      value: '0'
    }
  ]
  // 提交数据的字典
  private param: ParamsType = {
    area: '',
    buildingNo: '',
    floorPrice: '',
    name: '',
    price: '',
    projectId: '',
    roomNoList: [],
    roomTypeId: '',
    status: '1',
    toward: '',
    paymentMethod: ''
  }
  // 提交数据的字典copy
  private initParam: ParamsType = {
    area: '',
    buildingNo: '',
    floorPrice: '',
    name: '',
    price: '',
    projectId: '',
    roomNoList: [],
    roomTypeId: '',
    status: '1',
    toward: '',
    paymentMethod: ''
  }
  // 当前楼层的信息
  private building = {
    buildingNo: '',
    disableRoomCount: 0,
    enableRoomCount: 0,
    floorRoomCount: 0,
    maxFloor: 0,
    totalRoomCount: 0,
    unKnowRoomCount: 0
  }
  // 当前选中的房间
  private selectedRooms: any[] = []
  // 楼层信息
  private floorList: any[] = []
  // 房型列表
  private roomTypeList: any[] = []
  // 朝向列表
  private towardList: any[] = []
  // 支付方式
  private payMentMethodsList: any[] = []

  // 修改楼号
  private changebuildingNO(e: any) {
    if (this.selectedRooms.length > 0) {
      this.$msgbox({
        title: '警告',
        message:
          '有选择的房间尚未保存, 如果切换楼栋将不会保存您选择的房间信息, 是否继续?',
        showCancelButton: true,
        confirmButtonText: '确定',
        cancelButtonText: '取消'
      }).then(() => {
        this.selectedRooms = []
        this.getRoomInfo()
      })
    } else {
      this.getRoomInfo()
    }
  }
  /*
   * @Author: roct
   * @Description: 监听选中数组的变化, 当数组内只有一个的时候, 显示房间数据
   * @Date: 2021-06-05 14:28:49
   **/
  @Watch('selectedRooms', { immediate: true, deep: true })
  ChangeSelectedRooms(nl: any[]) {
    if (nl && nl.length === 1) {
      const roomInfo = nl[0]
      this.param.area = roomInfo.area
      this.param.buildingNo = roomInfo.buildingNo
      this.param.floorPrice = roomInfo.floorPrice
      this.param.price = roomInfo.price
      this.param.name = roomInfo.name
      this.param.projectId = roomInfo.projectId
      this.param.roomTypeId = roomInfo.roomTypeId
      this.param.status = roomInfo.status + ''
      this.param.toward = roomInfo.toward
      this.param.paymentMethod = roomInfo.paymentMethod + ''
    } else {
      this.param = JSON.parse(JSON.stringify(this.initParam))
    }
  }
  /**
   * @Author roct
   * @Description 点击保存按钮
   * @Date 23:43 2021/4/23 0023
   **/
  private async updateRoomListInfo() {
    // 过滤参数
    this.param.projectId = this.projectId
    // this.param.roomTypeId = this.roomTypeId
    this.param.buildingNo = this.buildingNo
    // TODO 没啥用 张良说晚上去掉这个参数
    // @ts-ignore
    this.param.id = '2222'
    for (const room of this.selectedRooms) {
      // @ts-ignore
      this.param.roomNoList.push(room.roomNo)
    }
    const res = this.checkParams()
    if (!res.status) {
      return this.$message.error(res.message)
    }
    const data = await updateRoomInfo(this.param)
    if (data.code === 200) {
      this.selectedRooms = []
      this.param = Object.assign({}, this.initParam)
      await this.getRoomInfo()
      this.$message.success('更新成功')
    } else {
      this.$message.error(data.msg)
    }
  }

  /**
   * @Author roct
   * @Description 参数过滤
   * @Date 23:52 2021/4/23 0023
   **/
  private checkParams() {
    if (!this.param.area) {
      return {
        status: false,
        message: '请输入平米'
      }
    }
    if (!this.buildingNo) {
      return {
        status: false,
        message: '请选择栋号'
      }
    }
    if (!this.param.floorPrice) {
      return {
        status: false,
        message: '请输入底价'
      }
    }
    if (!this.param.name) {
      return {
        status: false,
        message: '请输入房间主题'
      }
    }
    if (!this.param.paymentMethod) {
      return {
        status: false,
        message: '请选择支付方式'
      }
    }
    if (!this.param.price) {
      return {
        status: false,
        message: '请输入出租价'
      }
    }
    if (this.param.roomNoList.length <= 0) {
      return {
        status: false,
        message: '请选择要修改的房间'
      }
    }
    if (!this.param.status) {
      return {
        status: false,
        message: '请选择停用/启用'
      }
    }
    if (!this.param.toward) {
      return {
        status: false,
        message: '请选择朝向'
      }
    }
    return {
      status: true,
      message: ''
    }
  }

  /**
   * @Author roct
   * @Description 监听选择房型
   * @Date 0:06 2021/4/24 0024
   **/
  private changeRoomTypeIdInput(item: any) {
    const roomType = this.roomTypeList.find((target: any) => {
      return target.id === item
    })
    console.log('roomType', roomType)
    if (roomType && Number(roomType.status) === 1) {
      // if (!this.param.floorPrice) {
      this.param.floorPrice = roomType.floorPrice
      // }
      // if (this.param.price) {
      this.param.price = roomType.price
      // }
      // if (!this.param.area) {
      this.param.area = roomType.area
      // }
    }
  }

  /**
   * @Author roct
   * @Description 获取下拉列表等信息
   *
   * @Date 22:42 2021/4/17 0017
   **/
  private async getDictList() {
    // 获取楼栋列表
    const buildNoList = await getBuildingSelect({
      projectId: this.projectId,
      rootTypeId: this.roomTypeId
    })
    // console.log('buildNoList', buildNoList, this.projectId)
    this.buildNoList = buildNoList.data
    // 获取房型列表
    const res = await getRoomTypeList({ projectId: this.projectId })
    this.roomTypeList = res.data

    // 朝向列表
    const toward = await getDictSelect({ dictCode: 'toward' })
    this.towardList = toward.data
    // 支付方式
    const payment = await getDictSelect({ dictCode: 'payment_method' })
    this.payMentMethodsList = payment.data

    console.log('payMentMethodsList', this.payMentMethodsList)
  }

  /**
   * @Author roct
   * @Description 选中/取消选中item
   * @Date 21:46 2021/4/19 0019
   **/
  private selectedItemClick(item: any) {
    this.floorList = JSON.parse(JSON.stringify(this.floorList))
    if (item.selected) {
      const idx = this.selectedRooms.findIndex((target: any) => {
        return target.id === item.id
      })
      if (idx == -1) {
        this.selectedRooms.push(item)
      }
    } else {
      this.selectedRooms = this.selectedRooms.filter((target: any) => {
        return target.id !== item.id
      })
    }
  }

  /**
   * @Author roct
   * @Description 全选
   * @Date 21:30 2021/4/19 0019
   * @params
   * @return
   **/
  private allSelect(floorItem: any) {
    const idx = this.floorList.findIndex((target: any) => {
      return target.floor === floorItem.floor
    })
    if (idx !== -1) {
      this.floorList[idx] = floorItem
    }
    for (const item of floorItem.roomList) {
      if (item.selected) {
        const idx = this.selectedRooms.findIndex((target: any) => {
          return target.id === item.id
        })
        if (idx == -1) {
          this.selectedRooms.push(item)
        }
      } else {
        this.selectedRooms = this.selectedRooms.filter((target: any) => {
          return target.id !== item.id
        })
      }
    }
    this.floorList = JSON.parse(JSON.stringify(this.floorList))
  }

  /**
   * @Author roct
   * @Description 快速添加楼号
   * @Date 17:29 2021/4/18 0018
   **/
  private async fastAddClick(floor: number | string) {
    const res = await fastAdd({
      buildingNo: this.buildingNo,
      projectId: this.projectId,
      floor: Number(floor)
    })
    this.building = res.data.building
    this.floorList = res.data.floorList
    this.selectedRooms = []
  }

  /**
   * @Author roct
   * @Description 获取room信息
   * @Date 15:50 2021/4/18 0018
   **/
  private async getRoomInfo() {
    const res = await getRoomEditAll({
      buildingNo: this.buildingNo,
      projectId: this.projectId
    })
    this.projectNo = res.data.projectNo
    this.building = res.data.building
    this.floorList = res.data.floorList
    this.buildingNo = res.data.building.buildingNo
    // console.log('this.buildingNo', this.buildingNo)
  }

  public mounted(): void {
    this.getRoomInfo()
    this.getDictList()
  }
}
